import React from 'react'
import './aside.css'
import { rarity_list, exclusive_list, condition_list, effect_list } from '../data/select_options'

var rarity_option = []
var exclusive_option = []
var condition_option = []
var effect_option = []

function init_option(data, array) {
  array.push(<option key='-' value=''>-</option>)
  for (let item of data) {
    if (item.match(/#.*/)) {
      array.push(<option className='aside-option' key={item} value={item} disabled>{item.replace('#', '')}</option>)
    } else {
      array.push(<option key={item} value={item}>{item}</option>)
    }
  }
}

init_option(rarity_list, rarity_option)
init_option(exclusive_list, exclusive_option)
init_option(condition_list, condition_option)
init_option(effect_list, effect_option)

var inputs = {
  rarity: null,
  effect: null,
  condition: null,
  exclusive: null, 
  name: null
}

function Aside(props) {
  return (
    <div className={props.menuActive ? 'aside' : 'aside-disable'}>
      <div className='aside-item'>
        <div className='aside-label'>稀有度：</div>
        <select className="aside-input-item" ref={input => inputs.rarity = input}>
          {rarity_option}
        </select>
      </div>
      <div className='aside-item'>
        <div className='aside-label'>效果：</div>
        <select className="aside-input-item" ref={input => inputs.effect = input}>
          {effect_option}
        </select>
      </div>
      <div className='aside-item'>
        <div className='aside-label'>觸發條件：</div>
        <select className="aside-input-item" ref={input => inputs.condition = input}>
          {condition_option}
        </select>
      </div>
      <div className='aside-item'>
        <div className='aside-label'>專屬條件：</div>
        <select className="aside-input-item" ref={input => inputs.exclusive = input}>
          {exclusive_option}
        </select>
      </div>
      <div className='aside-item'>
        <div className='aside-label'>搜尋名稱：</div>
        <input className="aside-input-item" ref={input => inputs.name = input}></input>
      </div>
      <div className='aside-button-div'>
        <button onClick={() => props.handleSubmit(getInputsData(inputs))}>搜尋</button>
        <button onClick={() => {
          props.handleReset()
          reset()
        }} style={{float: 'right'}}>重置</button>
      </div>
    </div>
  )
}

function getInputsData(inputs) {
  let data = {}
  let keys = Object.keys(inputs)
  let values = Object.values(inputs)
  for (let i = 0; i < keys.length; i++) {
    data[keys[i]] = values[i].value
  }
  return data
}

function reset() {
  inputs.rarity.value = ''
  inputs.effect.value = ''
  inputs.condition.value = ''
  inputs.exclusive.value = ''
  inputs.name.value = ''
}

export default Aside